import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';


import { RmWgScale, RmWgScaleInf } from './services/wg-scale-model';
import { WgScaleService } from './services/wg-scale.service';


@Component({
  selector: 'app-wg-scale',
  templateUrl: './wg-scale.component.html',
  styleUrls: ['./wg-scale.component.css']
})
export class WgScaleComponent implements OnInit {

  rmwgscale : RmWgScale;
  rmwgsc = [];
  id: number;
  private sub: any;

  constructor(
    public wgServices: WgScaleService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number
      console.log('id = ' + this.id);
      //this.loadRMScale();
      if (isNaN(this.id)){
        console.log('load scale all');
        this.loadRMScaleAll();
      }else{
        this.loadRMScale()
      }
    });

   

  }

  loadRMScale(){
    console.log('begin load bmr formula wg scale' + this.id);
    this.wgServices.getWgitems(this.id).subscribe(
      rmwgscale => {
          console.log('success rm wg scale id : ' + this.id);
          this.rmwgsc = rmwgscale;
      },
      err => {
        console.log('error  ');
        console.log(err);
      },
      () => {
        console.log('complete!! loadRMScale');    
      }
    );
  }

  loadRMScaleAll(){
    console.log('begin load bmr formula wg scale');
    this.wgServices.getRMScaleAll().subscribe(
      rmwgscale =>{
        console.log('success rm wg scale all');
        console.log(rmwgscale)
        this.rmwgsc = rmwgscale;
      },
      err => {
  			console.log('error : ');
  			console.log(err);
  		},
  		  () => {console.log('Completed!')
      }
    );

    console.log('after search');
  }

  showItem(item: any){
    alert(item.isort);
    console.log('item : ' + item.RawmatID + '  ' + item.idWeight)
  }

  private ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



