import { Component } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import 'rxjs/Rx';


//service
import { NavbarService } from './services/system/navbar.service';


@Component({
  moduleId: module.id,
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title: string = 'app works!';

  //NAV ="";
  navTitle: string = '';
  navTitle_en: string = '';

  constructor(private nav: NavbarService){
  //constructor(){
    //this.NAV = nav.NAVBAR;
    this.nav.changes.subscribe((rest) => {
      this.navTitle = rest.navname;
      this.navTitle_en = rest.navnameen;

    });
  };

  selectedBtn: string;

  getStyle(Btn: string){
    this.selectedBtn = Btn;
    console.log('getStyle = ' + this.selectedBtn);
  }

  isIn = false;

  toggleState(){
    let bool = this.isIn
    this.isIn = bool === false ? true : false;

  }
  /*titleOnChange(val: string){
    this.NAV = val;
  }*/
  
}



