import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { MaterialModule } from '@angular/material';
//component
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { PoComponent } from './po/po.component';
import { PoDetailComponent } from './po/po-detail/po-detail.component';
import { ConfigComponent } from './config/config/config.component';

//services
import { MyConfigService } from './config/my-config.service';
import { NavHomeLinkService } from './services/system/nav-home-link.service';
import { DepartmentService } from './services/system/department.service';
import { BulkCategoryService } from './services/system/bulk-category.service';
import { NavbarService } from './services/system/navbar.service';

//routing
import { routing } from './app.routing';
import { PkgComponent } from './pkg/pkg.component';
import { FgComponent } from './fg/fg.component';
import { PlanComponent } from './plan/plan.component';
import { RdComponent } from './rd/rd.component';
import { PdComponent } from './pd/pd.component';
import { RmComponent } from './rm/rm.component';
import { RmOnhandComponent } from './rm/rm-onhand/rm-onhand.component';
import { BmrInvComponent } from './rm/bmr-inv/bmr-inv.component';
import { WgScaleComponent } from './rm/wg-scale/wg-scale.component';
import { WgScaleService } from './rm/wg-scale/services/wg-scale.service';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    PoComponent,
    PoDetailComponent,
    ConfigComponent,
    PkgComponent,
    FgComponent,
    PlanComponent,
    RdComponent,
    PdComponent,
    RmComponent,
    RmOnhandComponent,
    BmrInvComponent,
    WgScaleComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterialModule,
    routing
  ],
  providers: [
    NavHomeLinkService,
    DepartmentService, 
    MyConfigService, 
    BulkCategoryService,
    NavbarService,
    WgScaleService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



