import { Routes, RouterModule } from '@angular/router';

//component
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { PoComponent} from './po/po.component';
import { ConfigComponent } from './config/config/config.component';
import { PkgComponent } from './pkg/pkg.component';
import { RmComponent } from './rm/rm.component';
import { RmOnhandComponent } from './rm/rm-onhand/rm-onhand.component';
import { BmrInvComponent } from './rm/bmr-inv/bmr-inv.component';
import { WgScaleComponent } from './rm/wg-scale/wg-scale.component';
import { FgComponent } from './fg/fg.component';
import { RdComponent } from './rd/rd.component';
import { PdComponent } from './pd/pd.component';
import { PlanComponent } from './plan/plan.component';

//services
import { AuthGuardService } from './config/index';

const appRoutes: Routes = [
    { path: '', component: HomeComponent  }, //canActivate: [AuthGuardService]
    { path: 'login', component: LoginComponent },
    { path: 'register', component: RegisterComponent },
    { path: 'config', component: ConfigComponent },
    { path: 'po', component: PoComponent },
    { path: 'pkg', component: PkgComponent },
    { path: 'rm' ,component: RmComponent ,
        children:[
            {
                path:'', redirectTo:'onhand', pathMatch: 'full'
            },
            {
                path: 'onhand',
                component: RmOnhandComponent
            },
            {
                path: 'wgscale',
                component: WgScaleComponent
            },
            {
                path: 'wgscale/:id',
                component: WgScaleComponent
            },
            {
                path: 'bmr',
                component: BmrInvComponent
            }

        ]
     },
    { path: 'fg', component: FgComponent},
    { path: 'pd', component: PdComponent},
    { path: 'rd', component: RdComponent},
    { path: 'plan', component: PlanComponent},


    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes);


